import { createReducer } from '@reduxjs/toolkit'
import { forEach } from 'lodash'


import {
    showDrink, chooseDrink,
    setDrinksData, resetDrinks
} from './drinks-actions'

export const DRINKS_LIST_VIEW = 'DRINKS_LIST_VIEW'
export const DRINK_VIEW = 'DRINK_VIEW'

const initialState = {
    view: DRINKS_LIST_VIEW,
    drinks: [],
    drink: {}
}

const resetToInitialState = state => {
    forEach(initialState, (v, k) => state[k] = v);
}

const reset = state => {
    state.view = DRINKS_LIST_VIEW
    state.drinks = []
    state.drink = {}
}

// https://redux-toolkit.js.org/api/createreducer/
const drinkReducer = createReducer(initialState, {

    [showDrink]: (state, action) => {
        state.view = DRINK_VIEW
        state.drink = action.payload
    },

    [chooseDrink]: (state, action) => {
        reset(state)
    },

    [setDrinksData]: (state, action) => {
        resetToInitialState(state)
        state.drinks = action.payload.drinks
    },

    [resetDrinks]: (state, acion) => {
        resetDrinks(state)
    }
})

export default drinkReducer
