import React from 'react';
import { connect } from "react-redux";
import { showDrink } from './drinks-actions'
import './drinks-list.scss'

const stateToProps = state => {
    return { drinks: state.drinks.drinks };
};

function dispatchToProps(dispatch) {
    return { showDrink: drink => dispatch(showDrink(drink)) };
}

function DrinksList({ drinks, showDrink }) {
    if (!drinks.length) {
        return <ul className="drinks-list"><li>no drinks</li></ul>
    }

    return (<ul className="drinks-list">
        {drinks.map(d => (
            <li key={d.id}
                className="drinks-list"
                onClick={() => showDrink(d)}>

                <h3 className="drinks-list">
                    <span className="center">
                        {d.name}
                    </span>
                </h3>

                <div className="drinks-list">
                    {d.ingredients
                        .map((v, i, a) => {
                            const spacer = i < a.length - 1
                                ? <em className="drinks-list" >&nbsp;</em>
                                : ''

                            return (<>
                                <span className="drinks-list">{v}</span>
                                {spacer}
                            </>)
                        })
                    }
                </div>
            </li>
        ))
        }
    </ul >)
}

export default connect(stateToProps, dispatchToProps)(DrinksList);