import React from 'react'
import { connect } from 'react-redux'
import { chooseCategory } from './categories-actions'

const stateToProps = state => {
    return {
        categories: state.categories.categories
    };
};

function dispatchToProps(dispatch) {
    return {
        chooseCategory: cat => dispatch(chooseCategory(cat))
    };
}

function Categories({ categories, chooseCategory }) {

    const choices = categories.map(
        c => (
            <li key={c.id}
                className='categories'
                onClick={() => chooseCategory(c)}>

                <div className="categories">
                    <img className="center categories"
                        src={'categories/' + c.id + '.png'}
                        alt='{c.name}' >
                    </img>
                </div>

                <h2 className='categories' >
                    <span className='center'>{c.name}</span>
                </h2>

            </li>)
    )

    return <ul className='categoriess'>
        {choices}
    </ul>
}

export default connect(stateToProps, dispatchToProps)(Categories);
